let dev;
let admin;
let imgurl;
let qinniu;
// 开发环境
if (process.env.NODE_ENV === 'development') {
        dev='http://api.fydp.ushiji.club';
        admin ='http://admin.ushiji.club/admin';
        imgurl = 'http://static.ushiji.club';
        qinniu ='http://upload.qiniu.com'
// 编译环境
} else {
    // 测试环境
    if (process.env.type === 'test') {
        dev='http://api.fydp.ushiji.club';
        admin ='http://admin.ushiji.club/admin';
        imgurl = 'http://static.ushiji.club';
        qinniu ='http://upload.qiniu.com'
    // 正式环境
    } else {
        dev='http://api.fydianping.com';
        admin = 'https://ushiji.vip/admin';
        imgurl = 'https://static.ushiji.vip'
        qinniu ='https://upload.qiniu.com'
    }
}
 export default {
    host: dev,
    adminhost:admin,
    imgurls:imgurl,
    qinniu:qinniu,
 }


