<template>
    <div class="apply_wrap">
        <div class="success" v-show='success'>
            <div class="success_box">
                <img src="../../assets/website/images/wecahre.jpg" alt="">
                <p class="success_tip">扫描微信二维码</p>
                <p class="success_next">专人对接，完成入驻步骤</p>
                <!-- <a href="/" class="show_pc goindex"><< 返回首页</a> -->
                <div class='show_pc'>
                    <router-link to='/' tag='a' ><< 返回首页</router-link>
                </div>
                <div class='show_m'>
                    <router-link to='/' tag='a' >返回首页</router-link>
                </div>
            </div>
        </div>
         <div class="success reaffirm_wrap" v-show='reaffirm'>
            <div class="mask" style="position: absolute;width: 100%;height: 100%;background-color: rgba(0, 0, 0, 0.5);
    z-index: 201;" @click='reaffirm=false'></div>
            <div class="success_box reaffirm_txt" >
                <span class="erricon" @click='reaffirm=false'>X</span>
                <p class="info">确认信息</p>
                <div class="info_li">
                    <span>公司名称/个人</span>     
                    <span>{{full_name}}</span>    
                </div>  
                <div class="info_li">
                    <span>联系人姓名</span>     
                    <span>{{linkman}}</span>    
                </div> 
                <div v-if='wechat_id !=""' class="info_li">
                    <span>微信号</span>     
                    <span>{{wechat_id}}</span>    
                </div>   
                <div class="info_li">
                    <span>联系人手机号</span>     
                    <span>{{mobile_no}}</span>    
                </div>
                <div class="info_li">
                    <span>主营业务</span>     
                    <span>
                        <div v-for='item in old_business'>{{item}}</div>
                    </span>    
                </div>
                <div class=' again_save' @click='again'>
                   确定
                </div>
            </div>
        </div>
         <div class="mask"  v-show='laoding'>
            <div class="mask_loaidng">
                <div class="spinner">
                  <div class="spinner-container container1">
                    <div class="circle1"></div>
                    <div class="circle2"></div>
                    <div class="circle3"></div>
                    <div class="circle4"></div>
                  </div>
                  <div class="spinner-container container2">
                    <div class="circle1"></div>
                    <div class="circle2"></div>
                    <div class="circle3"></div>
                    <div class="circle4"></div>
                  </div>
                  <div class="spinner-container container3">
                    <div class="circle1"></div>
                    <div class="circle2"></div>
                    <div class="circle3"></div>
                    <div class="circle4"></div>
                  </div>
                </div>
                <p class="loaidng_txt" >{{loaidng}}</p>
            </div>
        </div>
        <div class="apply_content">
            <a class="header" href="/">
                <div class="nav">
                    <i class="logo "></i>
                    <span>商户入驻申请</span>
                </div>
            </a>
            <div class="apply_from">
                <div class="apply_boxs">
                    <h1 class="yw"><i>*</i>公司名称/个人</h1>
                    <div class="sun_input">
                        <input type="text" name="" v-model='full_name' placeholder="请输入公司名称或个人姓名" class="full_name texts boders">
                    </div>
                    <h1 class="yw"><i>*</i>联系人姓名</h1>
                    <div class="sun_input">
                        <input type="text" name="" class="texts Contacts boders" v-model='linkman' placeholder="请输入姓名">
                        <div class="err" v-if='errmsg==2'>
                            <i class="erricon" @click='errmsg = 0'></i>
                            <span>请输入姓名</span>
                        </div>
                    </div>
                    <h1 class="yw"><i>*</i>联系人手机号</br>
                    </h1>
                    <div class="sun_input" style="margin-bottom: .3rem;">
                        <input type="text" name="" class="texts Contacts boders" v-bind:class="{ success_phone: success_phone }"  v-model='mobile_no' placeholder="请输入手机号"  :disabled="success_phone">
                    </div>
                     <div class="sun_input" style="margin-bottom: 0;">
                       <input type="tel" pattern="[0-9]*" v-model="sms" placeholder="请输入验证码"  class="texts Contacts boders Contacts_init">
                        <button class="btn-sms " v-bind:class="{ btn_code: timeout }" :disabled="timeout" @click="getSms">{{ timeout? smsTime+'s后重新获取':'获取验证码' }}</button>
                    </div>
                    <p  class="phone_tips">注:此手机号将作为入驻商户登录DApp账号使用!</p>
                    <h1 class="yw">微信号</h1>
                    <div class="sun_input">
                        <input type="text" name="" class="texts Contacts boders" v-model='wechat_id' placeholder="请输入微信号">
                    </div>
                    
                    <h1 class="yw"><i>*</i>主营业务</h1>
                    <div class="business">
                        <div class="business_box">
                            <label  v-for='item in business' class="boders" :class="item.have? 'active':'er2'">
                                <input v-model='item.have'  type="checkbox" >
                                <img :src="item.img" alt="">
                                <span>{{item.name}}</span>
                            </label>
                        </div>
                        <div class="box boders inputs " :class="show_other?'active':''">
                            <input type="text" class="others" v-model='others' @focus="show_others" placeholder="其他" @blur='hide_other'>
                        </div>
                    </div>
                    <!-- <h1 class="yw">上传营业执照<span class="upimg_tip"> (选填)</span></h1>

                    <div class="business imgfilewrap">
                        <div class="uploadfile" v-if='imgs["license_img"]==""'>
                            <img src="../../assets/website/images/image_edit_upload.png" alt="">
                            <input type="file" name="upload"   id='imgLocal' class="file" ref="input" @change="fileChange('license_img',$event.target.files)">
                        </div>
                        <div class="img_box" v-else>
                            <i class="del" @click='delimg("license_img")'></i>
                            <img :src="imgs['license_img']+qiniuli" alt="">
                        </div>
                    </div>
                    <p  class="imgfile_tip" v-if='imgs["license_img"]==""'>仅限公司上传 !</p> -->

                    <div class="submit btn centers" @click='save'>确认提交</div> 
                    <div class="err" v-if='errmsg==100'>
                        <i class="erricon" @click='errmsg = 0'></i>
                        <span>{{err}}</span>
                    </div>   
                </div>  
            </div>
            
            <div class="rule_wrap">
                <div class="rule_box">
                    <h1>商户入驻规则</h1>
                    <h2>商户对象范围</h2>
                        <p>主要针对泛娱乐产业内的企业，重点包含体育竞技，影视文化，音乐产业，电竞游戏，文学阅读，网红明星，线下娱乐，新媒体营销，动漫二次元等领域的中小企业。我们暂时不接受本身就是平台性质的企业，主要为内容输出型企业为主。全国范围内均可，重点涵盖，北京，上海，杭州，广州，深圳，武汉，重庆等地。</p>
                        <h2>为什么要入驻？</h2>
                        <p>目前中小泛娱乐公司都面临着流量变现压力，而泛娱点评能通过区块链的通证经济解决如下问题</br>1. 100%流量变现，让原来的点赞，评论，分享等数据也能产生收益</br>2. 去中心化平台，绝不抽成</br>3. 和平台共同持有Token，获得更大的收益空间</p>
                        <h2>是否对我现在的业务有影响</h2>
                        <p>泛娱点评并不会对公司业务本身产生变化，只是让业务本身增值，用户也始终是属于该公司，同时，对于引流来说，泛娱点评将为各中小公司提供利器，用户能够通过点赞，评论，分享赚取NAST，可以不消费法币也可兑换服务和活动。真正的做到双赢。</p>
                        <h2>商户入驻条件</h2>
                        <p>1. 属于我们泛娱乐产业范畴</br>2. 合法经营，有相对应的营业执照及相关</br>3. 拥有线上及线下相对高频的内容和活动服务输出</br>4. 拥有流量运营经验，擅于导流，有用户群基础者更佳
                        </br>5. 同意在DAPP中统一用NAST结算，并愿意遵守平台规则</p>
                        <h2>入驻流程怎样，是否收费</h2>
                        <p>早期试运行阶段，为邀请制。商户需要收到平台负责人邀请后，提供相对应的信息和证件</br>1. 公司名称</br>2. 公司运营描述
                        </br>3.  目前社群及用户数</br>4.    公司营业执照</br>5.   服务联络人信息</p>
                        <p>收到该信息后，我们的客服人员将做记录备案，并在后台为商户开通账户，未来将为商户提供相应的SAAS服务系统</p>
                        <h2>一共需要多少家</h2>
                        <p>早期试运行阶段，我们限定100家商户入驻，后期视情况开放</p>
                        <h2>商户需要做哪些事情</h2>
                        <p>1.   完善商户基本信息</br>2. 上传活动或者服务（线上/线下均可）</br>3.    宣传推广吸引客户至DAPP</br>4.    为相关群内用户提供咨询答疑</br>5.    上传相关动态，信息告知</br>6.  遵守平台针对NAST的规则</p>
                        <h2>如何能够变现</h2>
                        <p>1.   商户可提NAST至交易所进行交易<br>2.  未来将推出C2C场外交易</p>
                        <h2>提币的具体规则</h2>
                        <p>1.    商户通过提供服务获得的NAST，30%将立即解锁，30%将于活动结束时解锁，40%将于活动结束后1个月后解锁<br>2.   商户通过被点赞，评论，分享获得的NAST，将分365天解锁<br>3. 商户自身通过交易所购买的NAST，将不受限制<br>4.    商户所获的NAST，在生态本身中消费，将不受任何限制</p>
                        <h2>如何能赚更多钱</h2>
                        <p>1.    持续持有NAST，持有更多NAST<br>2.    吸引更多的用户至平台<br>3.    教育用户去交易所购买NAST<br>4.    提高活动频率，活动质量，内容热度<br>5.  根据平台建议的统一建议行动</p>
                        </div>
                </div>
            <div class="footer">
                  2018 © 泛娱点评 
                  <p >沪ICP备18030575号-1</p> 
            </div> 
            <err-tip :tip_content='errtip' :tip_statie='tip_show'></err-tip>   
        </div>
    </div>
</template>
<script  type="text/javascript" charset="utf-8" >
import API from '../../api/api';  
import md5 from 'md5';
import utils from '@/assets/js/utils';
import errTip from '../../components/operation_tips'
export default{
     components:{
        errTip,
    },
    data(){
        return {
            success_phone:false,//手机号正确
            errtip:'',//错误提示
            tip_show:false, //显示
            sms:'',
            retime:null,
            // 重新获取短信倒计时时间
            smsTime:60,
            // 当前是否进入倒计时
            timeout: false,
            checkphone:false,
            reaffirm:false,//用户二次确认
            thum_qiniu:"",
            qiniuli:'',
            qiniubaseUrl:API.imgurls,
            action:API.qinniu,//地址
            token:'',//七牛token
            maximg:10,
            upimg:false,
            err:'',
            success:false,
            loaidng:'正在申请中,请您耐心等待!',
            show_other:false,
            laoding:false,
            errmsg:0,
            full_name:'',//公司全称
            linkman:'',//联系人姓名
            wechat_id:'',//微信
            mobile_no:'',//手机号
            business:[],//主营业务
            others:'',//自定义
            srcimg:['license_img','idnumbe_just','idnumbe_back','logo_img'],
            imgs: {
                'license_img':'','idnumbe_just':'','idnumbe_back':'','logo_img':''
            }, 
            old_business:'',
        }
    },
    mounted(){
        this.getToken();
        this.businessData();
        
    },
    created(){
       
    },
    methods:{
        reGetTimeout(){
            var self = this;
            if(self.smsTime >1){
                self.smsTime --;
                this.retime = setTimeout(()=>{
                    self.reGetTimeout();
                },1000)
            }else{
                self.smsTime = 60;
                self.timeout = false;
                clearTimeout(self.retime);
            }
        },
        getSms(){           
            //手机号正则
            let self = this;
            var phoneReg = /(^1\d{10}$)/;
            console.log(self.mobile_no)
            if(!phoneReg.test(self.mobile_no)){
                this.errfn('请输入正确的手机号码')
                return;
            }else {
                self.$axios.get(API.host+"/user/apply/check?mobile="+self.mobile_no).then(res=>{
                    console.log(res.data);
                    if(res.data.code=='200'){
                        this.checkphone=true;
                        this.success_phone =true;//手机号不能修稿
                        this.timeout = true;
                        this.reGetTimeout();
                        this.$axios({
                                method: 'get',
                                url: API.host+ '/user/sms/'+this.mobile_no
                            }).then(res=>{
                                if(res.data.code != '200'){
                                    this.errfn(res.data.msg)
                                    return;
                                }
                            });
                    }else{

                        this.errfn(res.data.msg)
                        return;
                    }
                }).catch(err=>{
                    console.log(err);
                    this.checkphone=false;
                }) 
            }
            
        },
        checkmobile(){
            let self= this;
            let phoneReg = /(^1\d{10}$)/;
            if(self.mobile_no !='' && phoneReg.test(self.mobile_no)){
                self.$axios.get(API.host+"/user/apply/check?mobile="+self.mobile_no).then(res=>{
                    console.log(res.data)
                    if(res.data.code=='200'){
                        this.checkphone=true;
                    }else{
                        this.errfn(res.data.msg)
                        return;
                    }

                }).catch(err=>{
                    console.log(err);
                    this.checkphone=false;
                }) 
            }else{
                // this.errfn('请填写正确的手机号码')
                return;
            }  
        },
        delimg(key){
            // 删除图片
            let self = this;
            self.imgs[key]='';
        },
        delallimg(){
            // 删除所有的图片
            var delimg =[];
            for(var i=0;i<this.srcimg.length;i++){
              var dbimgs = utils.dbGet('img_'+this.srcimg[i]);
              
              if(dbimgs == null || dbimgs ==''){}else{
                   delimg.push(dbimgs);
              }
            }
            if(delimg.length>0){
                this.$axios.post(API.host+"/upload/del",{files:delimg}).then(res=>{
                  for(var i=0;i<this.srcimg.length;i++){
                      utils.dbRemove('img_'+this.srcimg[i])
                  }
                  console.log(res.data);
                  
                }).catch(err=>{
                    console.log(err);
                })
            }
        },
        businessData(){
            let self = this;
            self.$axios.get(API.host+'/event/type').then((res)=>{
                console.log(res);
                if(res.data.code==200){
                    let data = res.data.data.list;
                   
                    data.forEach(function(val){
                        let datas = {
                            name:'',
                            hava:false,
                            img:val.img
                        }
                        datas.name=val.detail;
                        self.business.push(datas);
                    })
                    console.log(self.business)
                }
            }).catch((err)=>{
                console.log(err);   
            })
        },
        show_others(){
            this.show_other=true;
        },
        hide_other(){
            this.show_other=false;
        },
        filterHTMLTag(msg){
          var msg = msg.replace(/<\/?[^>]*>/g, ''); //去除HTML Tag
          msg = msg.replace(/[|]*\n/, '') //去除行尾空格
          msg = msg.replace(/&npsp;/ig, ''); //去掉npsp
          msg = msg.replace(/html|www|\.com|http|https/g, ''); //去掉npsp
          return msg;
        },
        save(){
            let self = this;
            let phoneReg = /(^1\d{10}$)/;
            let old_business =[];
            for(let i=0;i<self.business.length;i++){
                if(self.business[i].have){
                    old_business.push(self.business[i].name)
                }
            }
            if(self.others !=''){
               let txt =  self.filterHTMLTag(self.others)
                old_business.push(txt);
            }
            self.old_business = old_business;
            let postdata={
                smsCode:self.sms,
                org_name:self.full_name,
                linkman:self.linkman,
                mobile:self.mobile_no,
                wechat:self.wechat_id,
                main_business:self.old_business,
                
            }
            if(postdata.org_name==''){
                this.errfn('请输入公司名称或个人姓名')
                return;
            }else if(postdata.linkman==''){
                this.errfn('请输入联系人姓名')
                return;
            }else  if(postdata.mobile==''){
               this.errfn('请输入联系人手机号')
                return;
            }else if(!phoneReg.test(postdata.mobile)){
                this.errfn('请输入正确手机号')
                return;
            }else if(postdata.smsCode==''){
                this.errfn('请输入验证码')
                return;
            }else if(postdata.smsCode.length != 6 ){
                this.errfn('请输入6位手机验证码')
                return;
            }else if(postdata.main_business==''){
                this.errfn('请选择公司主营业务')
                return;
            }else {
                self.reaffirm = true;
            }
        },
        again(){
            let self = this;
            self.laoding = true;
            self.reaffirm =false;
            let postdata={
                smsCode:self.sms,
                org_name:self.full_name,
                linkman:self.linkman,
                mobile:self.mobile_no,
                wechat:self.wechat_id,
                main_business:self.old_business,
                license_img:'',
            }
            if(self.imgs['license_img'] != ""){
                // 图片上传
                let imgDatas= self.imgs['license_img'];//数据
                let img_key = self.mobile_no+'license_img';//名字
                let keys = 'images/admin/orgreg/'+md5(img_key);//加密之后的姓名
                let key = imgDatas.replace(/^data:image\/\w+;base64,/, "");
              
                let base64=window.btoa(keys)
                
                let config = {
                  headers:{
                    'Content-Type':'application/octet-stream',
                    'Authorization':"UpToken "+self.token
                    }
                };  
                //添加请求头
                self.$axios.post(API.qinniu+"/putb64/-1/key/"+base64,key,config)
                .then(res=>{
                    if(res.data.code='200'){
                           postdata.license_img = keys;
                           self.$axios.post(API.host+"/user/apply",postdata).then(res=>{
                                console.log(res.data)
                                if(res.data.code=='200'){
                                        this.loaidng = '申请成功!'
                                        self.org_name ='';
                                        self.linkman ='';
                                        self.mobile_no ='';
                                        self.imgs.license_img='';
                                        for(let i=0;i<self.business.length;i++){
                                            self.business[i].have=false;
                                        }
                                        self.others =''
                                        setTimeout(()=>{
                                            self.laoding = false;
                                            self.success = true;
                                        },1000)
                                }else if(res.data.code=='7'){
                                    self.laoding = false;
                                    this.success_phone =true;//手机号不能修稿
                                    this.errfn(res.data.msg);
                                    return;
                                }else{
                                    self.laoding = false;
                                    this.errfn(res.data.msg);
                                    return;
                                }

                            }).catch(err=>{
                                console.log(err);
                                this.errfn('上传失败');
                                self.laoding = false;
                            })
                    }else{
                        this.loaidng = '图片传失败!'
                        self.laoding = false;
                    }
                }).catch(err=>{
                    console.log(err);
                    this.loaidng = '图片传失败!'
                    self.laoding = false;
                })
            }else{
                self.$axios.post(API.host+"/user/apply",postdata).then(res=>{
                    console.log(res.data)
                        if(res.data.code=='200'){
                                this.loaidng = '申请成功!'
                                self.org_name ='';
                                self.linkman ='';
                                self.mobile_no ='';
                                self.imgs.license_img='';
                                for(let i=0;i<self.business.length;i++){
                                    self.business[i].have=false;
                                }
                                self.others =''
                                setTimeout(()=>{
                                    self.laoding = false;
                                    self.success = true;
                                },1000)
                        }else if(res.data.code=='7'){
                            self.laoding = false;
                            this.success_phone =true;//手机号不能修稿
                            this.errfn(res.data.msg);
                            return;
                        }else{
                            self.laoding = false;
                            this.errfn(res.data.msg);
                            return;
                        }

                    }).catch(err=>{
                        console.log(err);
                        this.errfn('上传失败');
                        self.laoding = false;
                    })
            }   
        },
        fileChange(key,files){
            let self =this; 
            self.upimg = true;
            let file = files[0]; 
            let imgtype = file.name.substring(file.name.lastIndexOf(".")+1).toLowerCase();
            console.log(imgtype)
            if(file.size>(self.maximg*(1024*1024))){
                this.errfn('请上传10M以内的图片！')
                return;
            }else if( imgtype !="jpg" && imgtype !="png" && imgtype !="gif" && imgtype != 'jpeg' && imgtype != 'bmp' && imgtype !="webp" && imgtype !="tiff"){
                this.errfn('上传图片格式错误！')
                return;
            }else{
                var reader = new FileReader();  // 创建FileReader对象

                  reader.readAsDataURL(file); // 读取file对象，读取完毕后会返回result 图片base64格式的结果

                  reader.onload = function(files){
                    self.imgs[key]=files.target.result;
                    console.log(files.target.result)
                  } 
                 self.upimg = false;
            }
        },
        getToken(){
            let self= this;
            // 获取token 
            self.$axios.get(API.host+'/upload/token',{
              params:{
                  type:0,
                  delete_file:false,
              }
              }).then(res => {
                  self.token = res.data.data.uploadToken;
              });
        },
        errfn(msg){
            // 弹出框
            let self = this;
            self.tip_show = true;
            self.errtip = msg;
            setTimeout(()=>{
              this.tip_show = false;
              this.errtip = ''
            },2000) 
        },
    },
}
