<template>
    
    <transition name="slide-fade"">
        <div class="operation_tipwrap" v-if='tip_statie'>
            <div class="err_tips" v-text='tip_content'>
            </div>
        </div> 
    </transition>
</template>
<script type="text/javascript" charset="utf-8">
    export default{
        props: ['tip_content','tip_statie'],
        data(){
            return{

            }
        }
    }
</script>
<style lang="scss" scoped>
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
    .operation_tipwrap{
        position: fixed;
        top: (111rem/37.5);
        left: 50%;
        transform: translate(-50%,0);
        
        .err_tips{
            padding: 0.26667rem 0.53333rem;
        font-size: 0.48rem;
        white-space: nowrap;
        color: #333333;
        font-weight: 400;
        background-color: rgba(202, 200, 200, 0.86);
        border-radius: 4px;
        }
    }


    /*PC*/
@media screen and (min-width:960px){
    .operation_tipwrap{
        position: fixed;
        top: (111rem/37.5);
        left: 50%;
        transform: translate(-50%,0);
        
        .err_tips{
            padding:10px 20px;
        font-size: 14px;
        white-space: nowrap;
        color: #333333;
        font-weight: 400;
        background-color: rgba(202, 200, 200, 0.86);
        border-radius: 4px;
        }
    }
}
</style>


